.NavBar {
    display: flex;
    flex-direction: column;
    background-color: #457b9d;
    color: white;
    padding: 20px;
    position: fixed;
    //height: 100vh;

    top: 0;
    left: 0;
    bottom: 0;
    width: 185px;
    transition: all ease-in-out 0.5s;
    z-index: 9997;
    overflow-y: auto;

    @media (max-width: 768px) {
        left: -225px;

        &.open {
            left: 0;
        }
    }

    .profile_wrapper {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 90px;
        .profile_image {
            display: flex;
            justify-content: center;
            align-items: center;
            //width: 120px;
            //height: 120px;
            //border-radius: 50%;
            //background-color: #2c2f3f;

            >div {
                width: 165px;
                height: 227px;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }

        }
        .name {
            font-family: "Poppins", sans-serif;
            font-size: 26px;
            font-weight: 600;
            margin-top: 10px;
            white-space: nowrap;
        }

        .links {
            display: flex;
            margin-top: 15px;

            .link-image {
                display: flex;
                align-items: center;
                cursor: pointer;
                text-decoration: none;
                color: white;
                padding: 5px;
                background-color: white;
                border-radius: 50%;
                margin-right: 7px;

                >img {
                    width: 20px;
                    height: 20px;
                }

                &:hover {
                    scale: 1.1;
                }
            }
        }
    }

    .menu {
        position: fixed;
        right: 15px;
        top: 15px;
        z-index: 9998;
        border: 0;
        font-size: 24px;
        transition: all 0.4s;
        outline: none !important;
        background-color: #149ddd;
        color: #fff;
        width: 40px;
        height: 40px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        line-height: 0;
        border-radius: 50px;
        cursor: pointer;

        @media (min-width: 768px) {
            display: none;
        }
    }

    .sidebar-tab {
        display: flex;
        color: white;
        text-decoration: none;
        font-size: 15px;
        margin-right: 40px;
        white-space: nowrap;
        margin-bottom: 30px;
        >svg {
            margin-right: 10px;
            font-size: 20px;
        }
        &:hover, &.active {
            font-weight: 700;
        }
    }

    .languages {
        display: flex;
        align-items: center;
        .lang:first-child {
            margin-right: 10px;
        }
        .lang {
            cursor: pointer;
            &.active {
                color: #0d6efd;
            }
        }
        .lang_separator {
            margin-right: 10px;
        }
    }
}
