.Footer {
  background-color: #457b9d;
  color: #fff;
  padding: 20px;
  text-align: center;
  bottom: 0;
  margin-top: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

  .links {
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
    .link-image {
      display: flex;
      flex-direction: column;
      align-items: center;
      cursor: pointer;
      
      text-decoration: none;
      color: white;
    }
  }

  .updated-date {
    margin-top: 20px;
    font-size: 14px;
  }
}
