.Home {
  display: flex;
  flex-direction: column;
  padding: 20px 40px 60px;
  background-color: aliceblue;

  h2 {
    font-family: "Raleway", sans-serif;
    color: #45505b;
  }

  > div:not(:first-child) {
    margin-top: 75px;
  }

  .Home__about-me {
    display: flex;
    flex-direction: column;


    .about-me {
      display: flex;
      flex-direction: column;

      .photo {
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        min-width: 160px;
        height: 250px;
        border-radius: 3px;
        margin-right: 30px;
      }

      .text {
        font-size: 17px;
        line-height: 1.5;
        margin-top: unset;
      }

      .info {
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        > svg {
          margin-right: 10px;
          height: 20px;
          width: 20px;
        }

        .location-icon {
          width: 25px;
          height: 25px;
          margin-right: 5px;
        }
      }
    }
  }

  a {
    color: #149ddd;
    text-decoration: none;
  }


  li {
    margin-bottom: 10px;
    font-size: 17px;
  }

  .linkunderline {
    color: unset;
    text-decoration: underline;
  }
}